<template>
    <div class="tabOne">
        <ul class="top-Ul">
            <li class="tapTitle" v-for="(tab,index) in tabs.tabs" :key="index" :class="{'active':tabindex===index}" @mouseenter="tabClick(index)">{{ tab }}</li>
            <li><a href="">···</a></li>
        </ul>
        <div class="contents" v-for="(tab,index) in tabs.tabVO" :key="index" v-show="tabindex===index" >
            <div class="title"><span>{{ tab.title }}</span></div>

            <ul>
                <li v-for="item in tab.data">
                    <span class="span2">
                        <span class="spanRed" v-if="item.color == 'red'">热门</span>
                        <span class="spanYellow" v-if="item.color == 'yellow'">公告</span>
                        <span class="spanblue" v-if="item.color == 'blue'">新闻</span>
                        {{ item.str }}
                    </span>
                    <span class="span3">{{ item.time }}</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    props: {

    },
    components: {

    },
    data() {
        return {
            tabs: [],
            tabindex:0
        };
    },
    computed: {

    },
    watch: {

    },
    created() {

    },
    mounted() {
        this.getNavList()
    },
    methods: {
        getNavList() {
            this.$http.get('./data/head.json').then((res) => {
                this.tabs = res.data
            })
        },
        tabClick(index){
            this.tabindex=index
        }
    }
}
</script>

