<template>
  <div class="Carousel">
    <!-- 轮播图 -->
    <div class="top">
      <div class="slides" :style="{ transform: 'translateX(' + translateX + 'px)' }">
      <div class="slide" v-for="(slide, index) in slides" :key="index">
        <img :src="slide.img" alt="">
      </div>
      </div>
    </div>
    <!-- 描述 -->
    <ul class="buttom" >
      <li 
       v-for="(content, index) in contents" 
       :key="index"
        :class="{ active: currentIndex === index }"
        @mouseenter="goToSlide(index)"
        @mouseleave="startAutoPlay(index)"
      >{{content.content}}</li>
    </ul>
  </div>
</template>

<script>

export default {
  data() {
    return {
      slides: [
        { img: require('@/assets/Carousel/1.jpeg'), index: 1 },
        { img: require('@/assets/Carousel/2.jpeg'), index: 2 },
        { img: require('@/assets/Carousel/3.jpeg'), index: 3 },
        { img: require('@/assets/Carousel/4.jpeg'), index: 4 },
        { img: require('@/assets/Carousel/5.jpeg'), index: 5 }

      ],
      contents: [
        { content: "KPL大名单", index: 1 },
        { content: "亚运电竞特许商品", index: 2 },
        { content: "K甲夏季赛赛程", index: 3 },
        { content: "小满的摸鱼日常", index: 4 },
        { content: "王者萌萌假日", index: 5 },
      ],
      currentIndex:0,
      translateX:0,
    };
  },
  methods: {
    nextSlide() {
      this.currentIndex = (this.currentIndex + 1) % this.slides.length;
      this.translateX = -this.currentIndex * 604; // Assuming each slide has a width of 100px
      this.i++
      if(this.i>this.contents.length-1)this.i=0
    },
    startAutoPlay() {
      this.timer = setInterval(this.nextSlide,3000);
    },
    stopAutoPlay() {
      // this.timer = setInterval(this.nextSlide, 600000000);
      clearInterval(this.timer);
    },
    goToSlide(index) {
      this.currentIndex = index;
      this.translateX = -index * this.slideWidth();
      this.stopAutoPlay()
    },
    slideWidth() {
      return document.querySelector('.slide').clientWidth;
    },
  }
  ,
  mounted() {
    this.startAutoPlay();
  },



};
</script>
<style lang="less" scoped>
.Carousel{
  width: 300px;
}
.top{
  width: 600px; /* Adjust the width as per your requirement */
  overflow: hidden;
  .slides {
  display: flex;
  transition: transform 0.5s;
  .slide {
  flex:300px;
  height: 298px; /* Assuming each slide has a width of 100px */
}
} 
}
.buttom{
  display: flex;
  width: 100%;
  height: 42px;
  background: #000;
  color: #c9c9dd;
  font-size: 14px;
  justify-content: space-between;
  align-items: center;
  li{
    flex-grow: 1;
    text-align: center;
    height: 100%;
    cursor: pointer;
    line-height: 42px;
  }
 
}
.buttom li.active {
  background: #2c2c2c;
    color: #ea9a05;
}
</style>