<template>
    <div class="contentC">
        <!-- 内容中心顶部 -->
        <div class="content-top">
            <div>
                <span class="logo"></span>
                <h2 style="font-weight: 600;font-size: 20px;">内容中心</h2>
            </div>
            <a href="#"><span class="plus"></span> 更多</a>
        </div>
        <!-- 内容中心tab切换 -->
        <div class="tab">
            <ul class="ul1">
                <li class="li1" v-for="(item, i) in tabs.tabs" :key="i" :class="{ 'active': tabIndex == i }"
                    @mouseenter="getindex(i)">
                    {{ item.name }}
                </li>
            </ul>
            <!-- 前两个tab -->
            <div class="tab-content" v-for="(item, index) in tabs.conList" :key="index" v-show="tabIndex == index">
                <ul class="ul2">
                    <li class="li2" v-for="(item1, index1) in item.tabs" :key="index1" :class="{ 'active': tabIndex1 == index1 }"
                        @mouseenter="getindex1(index1)">{{ item1 }}</li>
                </ul>
                <div class="tab-box" v-for="(item2, index2) in item.content" :key="index2" v-show="tabIndex1 == index2">
                    <div class="tab-item" v-for="(item3, index3) in item2.vo" :key="index3">
                        <img :src="item3.img" alt="">
                        <span>{{ item3.str }}</span>
                    </div>
                </div>
            </div>
            <!-- 第三个tab -->
            <div class="tab-content" v-show="tabIndex == 2" style="position:relative;">
                <span class="spanchoice" @mouseenter="choiceContent = true" >选择英雄<span
                        class="choice-icon"></span></span>
                <!-- 下拉框内容 -->
                <div class="choiceContent" v-if="choiceContent" style="position: absolute;z-index: 10;" @mouseleave="choiceContent = false">
                    <ul>
                        <li v-for="(choice,a) in tabs.choices" :key="a" @mouseenter="getsuper(a)" :class="{'active':superIndex == a}" >{{ choice.title }}</li>
                    </ul>
                    <div class="super" v-for="(sup,b) in tabs.super" :key="b" v-show="superIndex==b">
                        <ul v-for="items in sup">
                            <li v-for="item in items" ><img :src="item.img" alt="" ><span>{{item.name}}</span></li>
                        </ul>
                    </div>
                </div>
                <div class="tab-box">
                    <div class="tab-item" v-for="(itemLast, indexLast) in tabs.lastList" :key="indexLast">
                        <img :src="itemLast.img" alt="">
                        <span>{{ itemLast.str }}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {

    },
    components: {

    },
    data() {
        return {
            tabs: [],
            tabIndex: 0,
            tabIndex1: 0,
            choiceContent: false,
            choice:0,
            superIndex:0
        };
    },
    computed: {

    },
    watch: {

    },
    created() {

    },
    mounted() {
        this.getNavList()
    },
    methods: {
        getNavList() {
            this.$http.get('./data/contentTab.json').then((res) => {
                this.tabs = res.data
            })
        },
        getindex(i) {
            this.tabIndex = i
        },
        getindex1(i) {
            this.tabIndex1 = i
        },
        getsuper(a){
            this.superIndex=a
        }

    },
};
</script>

<style scoped lang="less">
.content-top {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;

    div {
        .logo {
            background: url(@/assets/headerImg/index.jpg) 0 -105px;
            display: inline-block;
            height: 25px;
            width: 25px;
            float: left;
        }

        h2 {
            margin-left: 10px;
            float: left;
        }

    }

    a {
        float: right;
        font-size: 16px;
        color: rgb(191, 195, 195);

        .plus {
            vertical-align: middle;
            display: inline-block;
            height: 15px;
            width: 15px;
            background: url(@/assets/headerImg/index.jpg) -251px 0;
            margin-right: 5px;
        }
    }
}

.tab {
    .ul1 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-grow: 1;
        background: rgb(233, 231, 231);

        .li1 {
            width: 100%;
            text-align: center;
            font-size: 16px;
            color: rgb(157, 159, 159);
            height: 30px;
            line-height: 30px;
            box-sizing: border-box;
            cursor: pointer;
        }

        .active {
            border-bottom: 3px solid #f3c258;
            color: #333333;
            background: #f5f5f5;
        }
    }

    .tab-content {
        width: 100%;
        height: 450px;

        .ul2 {
            margin-top: 10px;

            .li2 {
                height: 24px;
                line-height: 24px;
                padding: 0 13px 0 8px;
                border-radius: 10px;
                background: #f5f5f5;
                font-size: 14px;
                color: #333;
                margin-right: 8px;
                display: inline-block;
                margin-bottom: 5px;
                cursor: pointer;
            }

            .active {
                background: #f3c258;
                color: #e5e5e5;
            }
        }

        .tab-box {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            padding: 10px;
            box-sizing: border-box;

            .tab-item {
                width: 25%;
                margin-bottom: 20px;
            }
        }

        .spanchoice {
            font-size: 16px;
            margin-left: 10px;
            display: inline-block;
            text-indent: 8px;
            cursor: pointer;
            padding: 8px 0 9px;
        }

        .choice-icon {
            display: inline-block;
            height: 15px;
            width: 15px;
            background: url(../assets/headerImg/index.jpg)no-repeat -253px -22px;
        }
        .choiceContent{
            height: 330px;
            width: 965px;
            ul{
                float: left;
                display: flex;
                height: 330px;
                width: 70px;
                flex-wrap: wrap;
                background: #e5e5e5;
                li{
                    font-size: 14px;
                    width: 70px;
                    height: 40px;
                    text-align: center;
                    line-height: 40px;
                    
                }
                .active{
                    display: block;
                    background: #f3c258;
                    cursor: pointer;
                }
            }
            .super{
                float: left;
                display: flex;
                width: 895px;
                height: 330px;

                ul{
                    background: #f5f5f5;
                    width: 100%;
                    display: flex;
                    justify-content: start;
                    li{
                        margin: 10px 15px 0;
                        height: 100px;
                        img{
                            width: 100%;
                            height: 60%;
                            margin-bottom: -10px;
                        }
                    }
                }
            }
        }
    }
}</style>
